@import url(https://fonts.googleapis.com/css?family=Fira+Code&display=swap);
@font-face {
    font-family: 'Swiss921';
    src: local('Swiss921'), url(/static/media/Swiss921-BT_43276.6433bfa5.ttf) format('truetype');
}

@font-face {
    font-family: 'Korinna';
    src: local('Korinna'), url(/static/media/korinna.64dc7745.ttf) format('truetype');
}
.proceed-to {
    background-color: #001699;
    color: white;
    width: 500px;
    height: 50px;
    font: Arial;
    font-size: 28px;
    border-radius: 5px;
    border: none;
    margin-left: auto;
    margin-right: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    display: block;
}

.download {
    background-color: #404040;
    border: none;
    margin-top: 100px;
    border-radius: 3px;
}
.game-loader {
    text-align: center;
}

.game-loader h1 {
    font-family: 'Korinna', serif;
    font-size: 60px;
    margin-bottom: 10px;
}

.game-loader p {
    font-size: 20px;
    font-family: 'Korinna', serif;
}

.game-loader h2 {
    font-family: 'Korinna', serif;
    font-size: 30px;
}

hr {
    margin-top: 20px;
    margin-bottom: 50px;
    width: 200px;
}

.create-your-own {
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    font-family: sans-serif;
}
.player-chooser {
    font-family: 'Korinna-Bold';
}

.player-chooser input {
    height: 20px;
    margin: 10px;
    width: 200px;
}

.player-chooser button {
    width: 100px;
    height: 25px;
    border: none;
    border-radius: 3px;
}

.add-player-button {

}

.play-game-button {
    background-color: #61c27b;
}
body {
    background-color: black;
    color: white;
}

table {
    border: 3px solid black;
    border-collapse: collapse;
    table-layout: fixed;
    background-color: #001699;
    color: white;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    text-shadow: 2px 2px black;
}

td {
    border: 5px solid black;
    padding: 20px;
    width: 140px;
    text-align: center;
}

.category-container {
    height: 600px;
}

.category-box {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 600px;
    background-color: #001699;
    float: left;
}

.categorybox-enter {
    margin-left: 110%;
}

.categorybox-enter-active {
    margin-left: 0%;
    -webkit-transition: margin-left 1000ms ease;
    transition: margin-left 1000ms ease;
}

.categorybox-exit {
    margin-left: 0%;
    position: absolute;
}

.categorybox-exit-active {
    margin-left: -110%;
    -webkit-transition: margin-left 1000ms ease;
    transition: margin-left 1000ms ease;
}

.category-box .category {
    font-family: 'Swiss921', sans-serif;
    text-transform: uppercase;
    font-size: 120px;
    padding-left: 100;
}

td.category-title {
    font-family: 'Swiss921', sans-serif;
    font-size: 28px;
    text-transform: uppercase;
}

td.board-clue {
    font-family: 'Swiss921', sans-serif;
    font-size: 50px;
    height: 65px;
    color: #c99f57;
}

.clue {
    padding-left: 10px;
    padding-right: 10px;
}

.clue-display {
    background-color: #001699;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-family: 'Korinna', serif;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 50px;
    width: 100%;
    min-height: 600px;
    box-sizing: border-box;
    padding: 20px;
}

.final-category {
    font-family: 'Swiss921', sans-serif;
    font-size: 90px;
    font-weight: normal;
}

.daily-double {
    font-size: 90px;
}

.clue-category-label {
    color: white;
    font-family: 'Korinna', serif;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    font-size: 28px;
    padding: 10px;
}
.code {
    font-family: 'Consolas', 'Fira Code', monospace;
    text-transform: none;
}

.code-lines {
    list-style-type: none;
    font-family: 'Consolas', 'Fira Code', monospace;
    text-transform: none;
    text-align: left;
}
.scoreboard {
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-content: flex-start;
}

.podium {
    background-color: #001699;
    color: white;
    text-align: center;
    font-family: Arial;
    font-weight: bold;
    font-size: 28px;
    padding: 10px;
    min-width: 200px;
    border-radius: 5px;
}

.correct-answer, .incorrect-answer {
    min-width: 65px;
    height: 30px;
    padding: 2px;
    margin: 10px;
    font-family: Arial;
    font-weight: bold;
    font-size: 18px;
    border: 0px;
    border-radius: 5px;
}

.correct-answer {
    background-color: #21942a;
}

.incorrect-answer {
    background-color: #b53f18;
}

.stats {
    font-size: 18px;
}
